raul-container {
  display: block;
  position: relative;
  font-size: 0.875rem;
}

raul-container[add-shadow] {
  box-shadow: 0 2px 1px 0px rgba(55, 71, 79, 0.08);
}

raul-container[hoverable] {
  transition: all .2s ease-in-out;
}

raul-container[hoverable]:hover {
  box-shadow: 0 16px 27px 0 rgba(82, 97, 115, 0.22);
}

raul-container .r-container-wrapper {
  padding: 1.5rem;
  background-color: #fff;
  border-color: #EBEDEE;
  border-radius: 0.25rem;
  border-width: 1px;
  border-style: solid;
}

@media (min-width: 768px) {
  raul-container .r-container-wrapper {
    padding: 2rem;
  }
}

raul-container raul-container-image + .r-container-wrapper {
  border-top-width: 0;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

raul-container raul-handle {
  margin-left: 0.5rem;
  margin-right: 1rem;
}

raul-container.raul-container--movable, raul-container.raul-container--movable-by-handle {
  padding-left: 0;
}

raul-container.raul-container--movable:hover, raul-container.raul-container--movable-by-handle:hover {
  background-color: #E5F4FF;
}

raul-container.raul-container--movable:hover raul-handle, raul-container.raul-container--movable-by-handle:hover raul-handle {
  opacity: 1;
  min-height: 40px;
}

raul-container.raul-container--movable:hover {
  cursor: move;
}

raul-container.raul-container--movable-by-handle:hover raul-handle {
  cursor: move;
}